body {
  background: #fff;
}
.fed-roller {
  z-index: 1002;
  margin: 0 auto;
  bottom: -260px;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  display: block;
  bottom: 0;
  width: 100%;
  background: #fff;
  -webkit-user-select: none;
  -webkit-transform: translateZ(0);
  -webkit-user-drag: none;
  user-select: none;
  transform: translateZ(0);
  user-drag: none;
}
.fed-roller * {
  padding: 0;
  margin: 0;
}
.fed-roller-body {
  margin: 0 auto;
  width: 90%;
}
.fed-roller-body-top {
  width: 100%;
}
.fed-roller-body-top .fed-roller-title-content {
  background: #0b3c39;
  position: relative;
}
.fed-roller-body-top .fed-roller-title-content h3 {
  text-align: center;
  font-size: 16px;
  height: 42px;
  line-height: 42px;
  background: #0b3c39;
  color: #fff;
  margin: auto 50px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.fed-roller-body-top .fed-roller-cancel,
.fed-roller-body-top .fed-roller-ok {
  position: absolute;
  top: 50%;
  left: 0;
  color: #fff;
  padding: 5px 10px;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  z-index: 2;
}
.fed-roller-body-top .fed-roller-ok {
  left: auto;
  right: 0;
}
.fed-roller-content {
  display: block;
  position: relative;
  border-radius: 0;
  z-index: 20;
  overflow: hidden;
}
.fed-roller-content > p {
  height: 35px;
  border-color: #ccc;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  margin-top: -19px;
  pointer-events: none;
}
.fed-roller-content table {
  width: 100%;
}
.fed-roller-content .fed-roller-ul {
  height: 105px;
  position: relative;
  overflow: hidden;
}
.fed-roller-content .fed-roller-ul:after {
  content: "";
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: linear-gradient(#fff 0, rgba(245, 245, 245, 0) 52%, rgba(245, 245, 245, 0) 48%, #fff 100%);
  background: -webkit-gradient(linear, left bottom, left top, from(#fff), color-stop(0.52, rgba(245, 245, 245, 0)), color-stop(0.48, rgba(245, 245, 245, 0)), to(#fff));
  background: -webkit-linear-gradient(#fff 0, rgba(245, 245, 245, 0) 52%, rgba(245, 245, 245, 0) 48%, #fff 100%);
  background: -moz-linear-gradient(#fff 0, rgba(245, 245, 245, 0) 52%, rgba(245, 245, 245, 0) 48%, #fff 100%);
}
.fed-roller-content .fed-roller-larger .fed-roller-ul {
  height: 175px;
}
.fed-roller-content ul li {
  height: 35px;
  line-height: 35px;
  text-align: center;
}
.fed-roller-content ul li span {
  display: block;
  min-width: 32px;
}
.fed-roller-content ul .actives {
  font-size: 16px;
}
.fed-roller .clearfix:before,
.fed-roller .clearfix:after {
  display: table;
  content: "";
}
.fed-roller .clearfix:after {
  clear: both;
}



